@import url(https://fonts.googleapis.com/css2?family=Bigelow+Rules&display=swap);
body {
  margin: 0;
  min-height:100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(270deg,#5e17a1 0,rgb(5, 88, 126) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card-list{
    display:flex;
    flex-wrap:wrap;
    justify-content: center;
    margin:0 auto;
    height:650px;
    border:5px solid black;
    overflow-y:scroll;
    /* grid-template-columns: repeat(auto-fill, minmax(250px, 1fr)); */
}
.card-list::-webkit-scrollbar {
    display: none;
}
.card-container {
    display:inline-block;
    word-wrap: break-word;
    background-color: #0e7fbb;
    /* border: 1px solid rgb(252, 252, 252); */
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    -moz-osx-font-smoothing: grayscale;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    transition: transform 0.25s ease-out;
    width:350px;
    margin: .85rem 1.75rem;
    padding: 1rem;
    box-sizing: border-box;
    text-align: center;
    border-radius: .5rem;
    line-height: 1.15;
    box-shadow: 2px 2px 5px 0 rgba(255, 255, 255, 0.2);
  }
  .monster{
    -webkit-filter: grayscale(1) opacity(.8);
            filter: grayscale(1) opacity(.8);
    cursor: pointer;
    transition: all .2s ease-in;
  }
  .card-container:hover .monster{
      -webkit-filter:grayscale(0);
              filter:grayscale(0);
  }
  .card-container:hover {
    transform: scale(1.05);
  }
h2,p,img{
    pointer-events:none
}
.currentCard .monster{
    -webkit-filter:grayscale(0);
            filter:grayscale(0);
}
.currentCard{
    background-color: #68c6fd;
    box-shadow: none;
}
@media only screen and (max-width: 450px){
    .card-container{
        width:300px;
        margin:.85rem auto;
    }
}
@media only screen and (max-width:350px){
    .card-container{
        width:220px;
        margin:.85rem auto;
    }
    .monster{
        width:160px;
        height:auto;
    }
}

.searchField{
  font-size:1rem;
  padding: 1rem;
  background-color:#68c6fd;
  border: 2px solid #a9dafa;
  outline:none;
  line-height:15px;
  margin:30px;
}
.App {
  min-height:100vh;
  text-align: center;
}
h1{
  color:#68c6fd;
  margin:15px;
  font-family: 'Bigelow Rules', cursive;
  font-size:4.5rem;
}

